<template>
  <div class="content-message-item">
      <div class="left">
        <a :href="ContMItemData.note">
        <!--<a href="#">-->
          <img :src="ContMItemData.picture">
          <span >{{new Date(ContMItemData.title).getDate()}}<small>{{timeData}}</small></span>

        </a>
      </div>
      <div class="right">
        <a href="#">
          <p>{{ContMItemData.note}}</p>
          <span>每日一句    {{ContMItemData.viewCount}}次浏览</span>
        </a>
      </div>
    <!---------------------------------------------------------------------------------->
    <!--<div class="left"></div>-->
    <!--<div class="ssright"></div>-->
  </div>
</template>

<script>
  export default {
    name: "ContentMessageItem",
    data(){
      return {
        weekData:""
      }
    },
    props:{
      ContMItemData:{
        type:Object,
        default:function () {
          return {}
        }
      }
    },
    computed:{
      timeData(){
        let data = this.ContMItemData.title;
        let day = new Date(data).getDay();
        console.log(this.ContMItemData.title);
        console.log(day);
        if (day === 0){
          this.weekData = "Sun";
        }else if(day === 1){
          this.weekData = "Mon";
        }else if(day === 2){
          this.weekData = "Tue";
        }else if(day === 3){
          this.weekData = "Wed";
        }else if(day === 4){
          this.weekData = "Thur";
        }else if(day === 5){
          this.weekData = "Fri";
        }else if(day === 6){
          this.weekData = "Sat";
        }
        return this.weekData
      },

    }
  }
</script>

